<template>
  <div>
    <Bar title="Quin es su candidato?"/>
    <Container type="comparing">
      <div :class="$style.image"/>
      <div :class="$style.content">
        <div ref="container">
        </div>
      </div>

    </Container>
  </div>
</template>

<script>
  import Container from '../atoms/Container'
  import Bar from '../atoms/Bar'
  import Button from '../atoms/Button'

  export default {
    name: 'Test',
    components: {
      Container,
      Bar,
      Button
    },
    methods: {},
    computed: {
      candidate () {
        const candidate = this.$route.params.uid
        return this.$store.getters.getCandidateByUid(candidate)
      },
      comparing () {
        return this.$store.getters.isComparing()
      },
      isSingle () {
        return this.$route.params.uid
      },
      data () {
        return this.$store.getters.getMaquinaria(this.candidate.name)
      },
      comparisonItems () {
        const candidates = this.filter
        const result = []
        candidates.map((candidate, i) => {
          if (i > 4) return
          result.push({keyword: candidate, geo: '', time: 'today 12-m'})
        })
        return result
      }
    },
    data () {
      return {
        embed: '<h2>Not working yet.</h2>',
        filter: [
          'Ivn Duque',
          'Germn Vargas Lleras',
          'Sergio Fajardo',
          'Humberto de la Calle',
          'Gustavo Petro'
        ]
      }
    }
  }
</script>

<style module lang="scss">
  @import "../../assets/styles/base";

  .content {
    max-width: 937px;
    width: 100%;
    margin: 1em auto;

    p {
      font-family: $font__family--serif--especial;
      margin: 0 0 0.5em 0;
      font-size: 18px;
    }

    .highlight {
      color: $color__primary--base;
      font-family: $font__family--sans--especial;
      font-size: 12px;
      letter-spacing: 0.035em;
      font-weight: bold;
      margin-top: 2em;
    }
  }

  .image {
    background-image: url('../../assets/images/blur_photo.png');
    background-size: cover;
  }
</style>



